import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Div, Group, List, Cell, Avatar } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';

import Icon24Bug from '@vkontakte/icons/dist/24/bug';
import Icon24Help from '@vkontakte/icons/dist/24/help';
import Icon24Globe from '@vkontakte/icons/dist/24/globe';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';

import { fireEvent } from "../utils";

const sett = ({ id, back }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
                О сервисе
            </PanelHeader>
            <Group style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                padding: "25px 100px",
                textAlign: "center"
            }}>
                <Div>
                    <img src={require("../img/etrq.png")} alt="" style={{
                        width: "100%",
                        boxShadow: "0 0 25px #C165DD",
                        borderRadius: 35,
                        background: "#C165DD"
                    }}/>
                    <h3 style={{ margin: 0, padding: 0, paddingTop: 25 }}>Схема Метро</h3>
                    <h4 style={{ margin: 0, padding: 0, paddingTop: 5, color: 'rgba(0, 0, 0, .5)'}}>
                    </h4>
                </Div>
            </Group>
            <Group>
                <Div>
                    Схема Метро - приложение, в котором можно посмотреть схемы метро России. 
                    В приложении имеется вкладка, в которой можно почитать историю первого российского метрополитена. 
                </Div>
            </Group>
            <Group title="Ссылки">
                <List>
                    <Cell
                        expandable
                        onClick={() => fireEvent("https://vk.com/schemm")}
                        before={<Icon24LogoVk fill="#4bb34b" />}
                    >
                        <span style={{ color: "#4bb34b" }}>Официальная группа</span>
                    </Cell>
                </List>
            </Group>
        </Panel>
    );
};

const mapProps = () => ({});
const mapDispatch = ({ navigator: { goBack } }) => ({
    back: goBack
});

export default connect(mapProps, mapDispatch)(sett);