import React, { useEffect } from "react";
import { connect } from "react-redux";
import { View, ConfigProvider } from "@vkontakte/vkui";
import '@vkontakte/vkui/dist/vkui.css';

import Home from "./containers/Home";
import Profile from "./containers/Profile";
import Transaction from "./containers/Transaction";
import Top from "./containers/Top";
import About from "./containers/About";
import Stories from "./containers/Stories";
import Ekater from "./containers/Ekater";
import  Stor from "./containers/Stor";
import  Sett from "./containers/sett";
import  Nn2 from "./containers/nn2";
import  Ek2 from "./containers/Ek2";
import  Nov2 from "./containers/Nov2"; 
import  Kz2 from "./containers/Kz2"; 
import  St2 from "./containers/St2"; 
import  Msk2 from "./containers/Msk2"; 



const App = ({ activePanel, history, goBack, userLoad, popout, connect }) => {
	useEffect(() => {
		userLoad();
	}, [userLoad]);
	return (
		<ConfigProvider isWebView={true}>
			<View
				activePanel={activePanel}
				history={history}
				onSwipeBack={goBack}
				popout={popout}
			
		
			>
				<Home id="home"/>
				<Sett id="sett"/>
				<Top id="top"/>
				<Profile id="profile" />
				<Transaction id="transaction"/>
				<About id="about"/>
				<Ekater id="ekater"/>
				<Stories id="stories"/>
				<Nn2 id="nn2"/>
				<Stor id="stor"/>
				<Ek2 id="ek2"/>
				<St2 id="St2"/>
				<Nov2 id="nov2"/>
				<Kz2 id="kz2"/>
				<Msk2 id="Msk2"/>
				
		
			</View>
		</ConfigProvider>
			


			);
		};

const mapProps = (state) => ({
	activePanel: state.navigator.active,
	history: state.navigator.history,
	popout: state.navigator.popout
});

const mapDispatch = ({ navigator: { goBack }, user }) => ({
	goBack,
	userLoad: user.load
});

export default connect(mapProps, mapDispatch)(App);