import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, List, Avatar, FormLayout, formLoaded,  PanelSpinner,  Cell, Button, Div, FormStatus, Header, FixedLayou, TabsItem, Tabs, FixedLayout  } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24Message from '@vkontakte/icons/dist/24/message';
import { fireEvent } from "../utils";
import Icon24List from '@vkontakte/icons/dist/24/list';
import nn from '../img/nn.jpg';
import '@happysanta/vk-app-ui/dist/vkappui.css'
import { Notify, strong } from "@happysanta/vk-app-ui"
import piter from '../img/peterburg.jpg';
const St2 = ({ id, back, top, openProfile, go }) => {
    return (
        <Panel id={id}>
      <PanelHeader
               left={<PanelHeaderBack onClick={back} />}
            >
                 Санкт-Петербург
            </PanelHeader>
       

      <img className="" src={piter} alt=""/>
        </Panel>
    );
};
const mapProps = (state) => ({
    top: state.top
}); 
 
const mapDispatch = ({ navigator: { goBack },  navigator: { goForward }, transactions: { select, reset }, user: { load, openProfile }, cloud: { changeParam } }) => ({
  go: goForward,
  transactionSelect: select,
  resetTransactions: reset,
  loadUser: load,
    openProfile,
    back: goBack, 
  cloudUpdate: changeParam
});

export default connect(mapProps, mapDispatch)(St2);